import { resolveRestApi } from '../router/app-router';
import { fetch as axiosFetch, getCancelTokenSource, isFetchCanceled } from '../service_broker/fetch';

/**
 * The AttachmentUtils module contains utility APIs to handle the attachments of a case.
 * @module AttachmentUtils
 */

const UPLOAD_ATTACHMENT = 'uploadAttachment';
const LINK_CASE_ATTACHMENT = 'linkCaseAttachment';
const GET_CASE_ATTACHMENTS = 'getCaseAttachments';
const DOWNLOAD_ATTACHMENT = 'downloadAttachment';
const EDIT_ATTACHMENT = 'editAttachment';
const DELETE_ATTACHMENT = 'deleteAttachment';
const GET_ATTACHMENTS_CATEGORIES = 'getAttachmentCategories';
const DOWNLOAD_DOCUMENT = 'downloadDocument';
const DELETE_DOCUMENT = 'deleteDocument';
const cancelSources = {};

/**
 * Creates a cancel source, store it in an object and returns the cancel source.
 * @function
 * @static
 * @param {string} fileID The unique ID of file to use it as a key to store the cancel source
 * @private
 * @returns {object} cancelSource
 */
const getNewCancelSource = (fileID) => {
  const cancelSource = getCancelTokenSource();
  cancelSources[fileID] = cancelSource;
  return cancelSource;
};

/**
 * This API cancels an ongoing XML HTTP Request (XHR) for a file that is being uploaded to a server or linked to a case.
 * @function
 * @static
 * @param {string} fileID The unique ID of the file that is used to obtain the cancel source to cancel the ongoing XHR request.
 *
 * @example <caption>In this example, the API cancels the request for the file with ID '123456' that is being uploaded to the server.</caption>
 * PCore.getAttachmentUtils().cancelRequest('123456');
 *
 * @returns {void}
 */
const cancelRequest = (fileID) => {
  /* istanbul ignore next */
  if (cancelSources[fileID]) {
    cancelSources[fileID].cancel();
    delete cancelSources[fileID];
  }
};

/**
 * This API uploads a file to the server.
 * @function
 * @static
 * @example <caption>In this example, the API uploads a file to the server.</caption>
 * const onUploadProgress = (ev) => {
 *  // ev is a progress type event
 *  // which has total, loaded properties to calculate progress
 * }
 *
 * const errorHandler = (error) => {
 *  // handle error here
 * }
 *
 * PCore.getAttachmentUtils().uploadAttachment(file, onUploadProgress, errorHandler, 'app/primary_1')
 * .then((fileResponse) => {
 *   ({
    type: 'File',
    category: file.category || 'File',
    ID: fileResponse.data.ID,
    clientFileID: file.ID
  })
 * }).catch(err => {
 *    // Error handling
 * });
 *
 * @param {object} file The file that needs to be uploaded to the server.
 * @param {Function} onUploadProgress A callbackfunction that obtains the upload progress of the file.
 * @param {Function} errorHandler A callback function to handle exceptions.
 * @param {string} context The name of the context where the API is being called.
 * @returns {Promise} The Promise
 */

const uploadAttachment = (file, onUploadProgress, errorHandler, context) => {
  const formData = new FormData();
  formData.append('appendUniqueIdToFileName', true);
  formData.append('file', file);
  const { restApi, reqType } = resolveRestApi(UPLOAD_ATTACHMENT, {});
  return axiosFetch(
    restApi,
    {
      method: reqType,
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      body: formData,
      onUploadProgress,
      signal: getNewCancelSource(file.ID).token
    },
    context
  )
    .then((fileResponse) => ({
      type: 'File',
      category: file.category || 'File',
      ID: fileResponse.data.ID,
      clientFileID: file.ID
    }))
    .catch(
      errorHandler
        ? errorHandler(isFetchCanceled, file)
        : (error) => {
            error.isHandled = false;
            throw error;
          }
    )
    .finally(() => delete cancelSources[file.ID]);
};

/**
 * This API links the already uploaded file or an array of links to the case.
 * @function
 * @static
 * @param {string} caseID The ID of the case pointing to a pzInsKey value to which the uploaded attachment needs to be linked.
 * @param {Array} attachments A file or an array of links that need to be linked to the case.
 * @param {string} attachmentType The type of attachments(File, URL, etc) that we are trying to link them to the case.
 * @param {string} context The name of the context where the API is being called.
 *
 * @example <caption>In this example, the API links the uploaded file whose ID is '234545' to the case whose ID is 'W-102'.</caption>
 * PCore.getAttachmentUtils().linkAttachmentsToCase('ORG-MYAPP-WORK W-102', [{
 *  "type": "File",
 *  "category": "Screenshot",
 *  "attachmentFieldName": "Screenshot",
 *  "fileType": "PNG",
 *  "name": "Screenshot for this issue",
 *  "ID": "234545"
 * }], "File", "app/primary_1")
 * .then((attachments) => {
 *   // attachments
 * }).catch(err => {
 *    // Error handling
 * });
 * @returns {object} The Promise
 */
const linkAttachmentsToCase = (caseID, attachments, attachmentType, context) => {
  const reqInfo = {};
  // We are linking one file at a time but multiple urls can be attached at a time
  // So, if attachmentType is File then processing one at a time
  if (attachmentType === 'File') {
    const [attachmentInfo] = attachments;
    const { clientFileID } = attachmentInfo;
    delete attachmentInfo.clientFileID;
    reqInfo.signal = getNewCancelSource(clientFileID).token;
  }

  const { restApi, reqType } = resolveRestApi(LINK_CASE_ATTACHMENT, { caseID });

  return axiosFetch(
    restApi,
    {
      ...reqInfo,
      body: { attachments },
      method: reqType
    },
    context
  );
};

/**
 * This API obtains the attachments linked to a case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the attachments linked to a case whose ID is W-102.</caption>
 * PCore.getAttachmentUtils().getCaseAttachments('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(attachments => {
 *    // attachments array
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID The ID of the case pointing to pzInskey value whose attachments must be obtained.
 * @param {string} context The name of the context where the API is being called.
 * @param {Boolean} includeThumbnail The flag that determines if the thumbnail associated with the attachment 
 *                                   is returned. The default value is false.If includeThumbnail is true, the 
 *                                   thumbnail associated with the attachment is returned as a property of the 
 *                                   attachment object.If includeThumbnail is false, the thumbnail associated 
 *                                   with the attachment is not returned.
 * @returns {Promise} The Promise
 */
const getCaseAttachments = (caseID, context, includeThumbnail = false) => {
  const { restApi, reqType } = resolveRestApi(GET_CASE_ATTACHMENTS, {
    caseID,
    includeThumbnail
  });

  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  ).then((response) => response.data.attachments);
};

/**
 * This API obtains the attachment cateogries linked to a case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the attachments cateogries linked to a case whose ID is W-102.</caption>
 * PCore.getAttachmentUtils().getAttachmentCategories('ORG-MYAPP-WORK W-102', 'file')
 * .then(attachments => {
 * // attachments array
 * }).catch(err => {
 * // errors
 * });
 *
 * @param {string} caseID The ID of the case pointing to pzInsKey value whose attachment categories must be obtained.
 * @param {string} type The type of attachment.
 * @param {string} context The name of the context where the API is being called.
 * @returns {Promise} The Promise
 */
const getAttachmentCategories = (caseID, type, context) => {
  const { restApi, reqType } = resolveRestApi(GET_ATTACHMENTS_CATEGORIES, { caseID, type });

  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  ).then((response) => response.data);
};

/**
 * This API downloads the binary content of an attachment.
 * @function
 * @static
 * @example <caption>In this example, the API returns a Promise that obtains the binary content of the attachment whose ID is '123445'.</caption>
 * PCore.getAttachmentUtils().downloadAttachment('123445', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} attachmentID The ID of the attachment whose binary content needs to be downloaded.
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const downloadAttachment = (attachmentID, context, responseEncoding) => {
  const { restApi, reqType } = resolveRestApi(DOWNLOAD_ATTACHMENT, {
    attachmentID
  });

  return axiosFetch(
    restApi,
    {
      method: reqType,
      ...(responseEncoding === 'binary' && { responseType: 'arraybuffer' })
    },
    context
  );
};

/**
 * This API edits an attachment from case to which it is linked.
 * @function
 * @static
 * @example <caption>In this example, he API edits the attachment Name and Category whose ID is '123445'.</caption>
 * PCore.getAttachmentUtils().editAttachment('123445', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} attachmentID The ID of the attachment that needs to be deleted.
 * @param {Map<string, any>} attachmentMetaData The Name and Category in a object format {name: "", category: ""}
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const editAttachment = (attachmentID, attachmentMetaData, context) => {
  const { restApi, reqType } = resolveRestApi(EDIT_ATTACHMENT, {
    attachmentID
  });

  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: { ...attachmentMetaData }
    },
    context
  );
};

/**
 * This API deletes an attachment from case to which it is linked.
 * @function
 * @static
 * @example <caption>In this example, he API deletes the attachment whose ID is '123445'.</caption>
 * PCore.getAttachmentUtils().deleteAttachment('123445', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} attachmentID The ID of the attachment that needs to be deleted.
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const deleteAttachment = (attachmentID, context) => {
  const { restApi, reqType } = resolveRestApi(DELETE_ATTACHMENT, {
    attachmentID
  });

  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  );
};

/**
 * This API downloads the binary content of an document.
 * @function
 * @static
 * @example <caption>In this example, the API returns a Promise that obtains the binary content of the document whose ID is '123445'.</caption>
 * PCore.getAttachmentUtils().downloadDocument('123445', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} documentID The ID of the document whose binary content needs to be downloaded.
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const downloadDocument = (documentID, context) => {
  const { restApi, reqType } = resolveRestApi(DOWNLOAD_DOCUMENT, {
    documentID
  });

  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  );
};

/**
 * This API deletes an document from case to which it is linked.
 * @function
 * @static
 * @example <caption>In this example, the API deletes the document whose ID is '123445' from case 'C-1'.</caption>
 * PCore.getAttachmentUtils().deleteDocument('ORG-MYAPP-WORK C-1', '123445', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID The ID of the case pointing to pzInsKey value.
 * @param {string} documentID The ID of the document that needs to be deleted.
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const deleteDocument = (caseID, documentID, context) => {
  const { restApi, reqType } = resolveRestApi(DELETE_DOCUMENT, {
    caseID,
    documentID
  });

  return axiosFetch(
    restApi,
    {
      method: reqType
    },
    context
  );
};

/**
 * Gets URL for opening documents in infinity application
 * @function
 * @static
 * @example <caption>In this example, the API get the URL for documents.</caption>
 * PCore.getAttachmentUtils().getDocumentsURL('app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getDocumentURL = (c11nEnv) => {
  const dataViewName = PCore.getEnvironmentInfo().getDocumentUrlDP() || 'D_pxGetDocumentURL';
  return PCore.getDataPageUtils()
    .getPageDataAsync(dataViewName, c11nEnv.getContextName(), {}, { invalidateCache: true })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call getDocumentURL API : ', error.response);
    });
};

export default {
  uploadAttachment,
  linkAttachmentsToCase,
  getCaseAttachments,
  getAttachmentCategories,
  downloadAttachment,
  editAttachment,
  deleteAttachment,
  downloadDocument,
  deleteDocument,
  getDocumentURL,
  cancelRequest
};
