import { invokeRestApi } from '../rest-client';

import updateETagToReduxStore from './utils';

/**
 * Use the API in the StakeholderUtils module to handle the participants of a case.
 * @module StakeholderUtils
 */

const GET_PARTICIPANTS = 'getAllParticipants';
const GET_PARTICIPANT_ROLES = 'getParticipantRoles';
const GET_PARTICIPANT_ROLE_VIEW = 'getParticipantRoleView';
const GET_PARTICIPANT = 'getParticipant';
const CREATE_PARTICIPANT = 'createParticipant';
const UPDATE_PARTICIPANT = 'updateParticipant';
const DELETE_PARTICIPANT = 'deleteParticipant';

/**
 * Obtains the list of participants for a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the list of participants of a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().getParticipants('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(participants => {
 *    // participants array
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose  list of participants must be obtained.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getParticipants = (caseID, c11nEnv) => {
  const queryPayload = {
    caseID
  };
  return invokeRestApi(GET_PARTICIPANTS, { queryPayload }, c11nEnv.getContextName())
    .then((response) => {
      return response?.data?.data.content.pyParticipants;
    })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at getParticipants API : ', error.response);
    });
};

/**
 * Obtains the list of participant roles for a specific case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the list of participants of a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().getParticipantRoles('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(roles => {
 *    // roles array
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case poiting to pzInsKey value whose  list of participants must be obtained.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getParticipantRoles = (caseID, c11nEnv) => {
  const queryPayload = {
    caseID
  };
  return invokeRestApi(GET_PARTICIPANT_ROLES, { queryPayload }, c11nEnv.getContextName())
    .then((response) => {
      return response?.data?.participant_roles;
    })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at getParticipantRoles API : ', error.response);
    });
};

/**
 * Gets participant data by ID.
 * @function
 * @static
 * @example <caption>In this example, the API gets the data of participant with participant ID 'participant@org.com', linked to a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().getParticipant('ORG-MYAPP-WORK W-102','participant@org.com', 'app/primary_1')
 * .then(participantData => {
 *    // participantData
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose  list of participants must be obtained.
 * @param     {string}    participantID   The string containing the participant ID.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getParticipant = (caseID, participantID, c11nEnv) => {
  const queryPayload = {
    caseID,
    participantID
  };

  return invokeRestApi(GET_PARTICIPANT, { queryPayload }, c11nEnv.getContextName())
    .then((response) => response?.data)
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at getParticipant API : ', error.response);
    });
};

/**
 * Creates a new participant.
 * @function
 * @static
 * @example <caption>In this example, the API creates a new participant of a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().createParticipant('ORG-MYAPP-WORK W-102','Resource', participantData, 'app/primary_1')
 * .then(newParticipantData => {
 *    // newParticipantData
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose  list of participants must be obtained.
 * @param     {string}    participantRoleID   The string containing the new participant role Id.
 * @param     {object}    participantData   Data object containing participant details.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const createParticipant = (caseID, participantRoleID, participantData, c11nEnv) => {
  const queryPayload = {
    caseID
  };

  const { content } = participantData;

  const body = {
    content,
    participantRoleID
  };

  const headers = {
    'if-match': c11nEnv.getCaseSummary().headers.etag
  };

  return invokeRestApi(CREATE_PARTICIPANT, { body, queryPayload, headers }, c11nEnv.getContextName())
    .then((response) => {
      updateETagToReduxStore(response.headers, c11nEnv);
    })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at createParticipant API : ', error.response);
    });
};

/**
 * Updates an existing participant.
 * @function
 * @static
 * @example <caption>In this example, the API updates an existing participant of a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().updateParticipant('ORG-MYAPP-WORK W-102','participant@org.com', participantData, 'app/primary_1')
 * .then(updatedParticipantData => {
 *    // updatedParticipantData
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose  list of participants must be obtained.
 * @param     {string}    participantID    The ID of the participant to edit.
 * @param     {object}    participantData   The object containing the new participant data.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const updateParticipant = (caseID, participantID, participantData, c11nEnv) => {
  const { content } = participantData;

  const body = {
    content
  };
  const queryPayload = {
    caseID,
    participantID
  };

  const headers = {
    'if-match': c11nEnv.getCaseSummary().headers.etag
  };
  return invokeRestApi(UPDATE_PARTICIPANT, { body, queryPayload, headers }, c11nEnv.getContextName())
    .then((response) => {
      updateETagToReduxStore(response.headers, c11nEnv);
    })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at updateParticipant API : ', error.response);
    });
};

/**
 * Deletes an existing participant.
 * @function
 * @static
 * @example <caption>In this example, the API deletes an existing participant of a case whose ID is W-102.</caption>
 * PCore.getStakeholderUtils().deleteParticipant('ORG-MYAPP-WORK W-102', 'participant@org.com', 'app/primary_1')
 * .then(updatedParticipantData => {
 *    // updatedParticipantData
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose list of participants must be obtained.
 * @param     {string}    participantID    The ID of the participant to edit.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const deleteParticipant = (caseID, participantID, c11nEnv) => {
  const queryPayload = {
    caseID,
    participantID
  };

  const headers = {
    'if-match': c11nEnv.getCaseSummary().headers.etag
  };

  return invokeRestApi(DELETE_PARTICIPANT, { queryPayload, headers }, c11nEnv.getContextName())
    .then((response) => {
      updateETagToReduxStore(response.headers, c11nEnv);
    })
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at deleteParticipant API : ', error.response);
    });
};

/**
 * Obtains the view for a specific role.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the list of participants of a case whose ID is W-102 and role 'Owner'.</caption>
 * PCore.getStakeholderUtils().getRoleView('ORG-MYAPP-WORK W-102', 'Owner', 'app/primary_1')
 * .then(view => {
 *    // role view
 * }).catch(err => {
 *   // errors
 * });
 * @param     {string}    caseID    The ID of the case pointing to pzInsKey value whose  list of participants must be obtained.
 * @param     {string}    participantRoleID    Role ID whose view to get.
 * @param     {object}    c11nEnv   The name of the context where the API is being called.
 * @returns   {Promise<object>}     A Promise that resolves to an object.
 */
const getRoleView = (caseID, participantRoleID, c11nEnv) => {
  const queryPayload = { caseID, participant_roleID: participantRoleID };
  return invokeRestApi(GET_PARTICIPANT_ROLE_VIEW, { queryPayload }, c11nEnv.getContextName())
    .then((response) => response?.data)
    .catch((error) => {
      c11nEnv.reportError(': Error ocurred during ajax call at fetchTaggedcases API : ', error.response);
    });
};

export default {
  getParticipants,
  getRoleView,
  getParticipantRoles,
  getParticipant,
  createParticipant,
  updateParticipant,
  deleteParticipant
};
