import { invokeRestApi } from '../rest-client';

/**
 * Provides APIs to work on Followers.
 * These APIs are used to add/delete/get case followers.
 * @module CaseFollowerApi
 */

const ADD_CASE_FOLLWERS = 'addCaseFollower';
const DELETE_CASE_FOLLOWER = 'deleteCaseFollower';
const GET_CASE_FOLLOWERS = 'getCaseFollowers';

/**
 * Returns a Promise that adds a user as follower to the case.
 * @function
 * @static
 * @param {string} caseID The ID of the case pointing to pzInsKey value to which the user need to be added as follower.
 * @param {string} userID The ID of the user which needs to be added added as follower to a case.
 * @param {string} context The name of the context where the API is being called
 *
 * @example <caption>In this example, the API adds a user whose ID is user-1 as follower to the case whose ID is W-102.</caption>
 * PCore.getCaseFollowerApi().addCaseFollower('ORG-MYAPP-WORK W-102', 'user-1', 'app/primary_1')
 * .then(() => {
 *   // success
 * }).catch(err => {
 *    // Error handling
 * });
 * @returns {object} The Promise
 */
const addCaseFollower = (caseID, userID, context) => {
  const queryPayload = {
    caseID
  };
  const reqBody = JSON.stringify({
    users: userID.map((user) => ({ ID: user }))
  });
  return invokeRestApi(ADD_CASE_FOLLWERS, { body: reqBody, queryPayload }, context);
};

/**
 * Deletes the follower of a case.
 * @function
 * @static
 * @example <caption>In this example, he API deletes the follower whose ID is user-1 for case whose ID is W-02.</caption>
 * PCore.getCaseFollowerApi().deleteCaseFollower('ORG-MYAPP-WORK W-02', 'user-1', 'app/primary_1')
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID An ID of the case pointing to pzInsKey value for which we want to delete the follower
 * @param {string} followerID An ID of the follower which we want to delete for a case
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const deleteCaseFollower = (caseID, followerID, context) => {
  const queryPayload = {
    caseID,
    followerID
  };
  return invokeRestApi(DELETE_CASE_FOLLOWER, { queryPayload }, context);
};

/**
 * Returns a Promise that gets the followers of a case.
 * @function
 * @static
 * @example <caption>In this example, the API obtains the followers of a case whose ID is W-102.</caption>
 * PCore.getCaseFollowerApi().getCaseFollowers('ORG-MYAPP-WORK W-102', 'app/primary_1')
 * .then(followers => {
 *    // followers array
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param {string} caseID The ID of the case pointing to pzInsKey value whose followers must be obtained.
 * @param {string} context The name of the context where the API is being called
 * @returns {Promise} The Promise
 */
const getCaseFollowers = (caseID, context) => {
  const queryPayload = { caseID };
  return invokeRestApi(GET_CASE_FOLLOWERS, { queryPayload }, context).then((response) => response.data.followers);
};

export default {
  addCaseFollower,
  deleteCaseFollower,
  getCaseFollowers
};
